import { Component,OnInit, Input,Output ,EventEmitter} from '@angular/core';
import {ApiService} from '../_services/api.service';
import { copyStyles } from '@angular/animations/browser/src/util';
//import {UtilService} from '../shared/utilservice'
import {RawMaterialCfg, RawMaterialImp} from '../_models'


@Component({
  selector: 'rawitem',
  templateUrl: './raw_material.html',
  styleUrls: ['./common.css']
})
export class RawMaterial implements OnInit {
 uri = 'finishedproduct'
 rawItemCfg:Array<RawMaterialCfg>
 materialmap:Map<string,string> ;
 sizemap :Map<string,string>;
 selectedMaterial:string
 apiresult = null
 dataTosave:Array<RawMaterialImp>=[]
 weight:number
 selectedsize:string
 constructor(private apiService:ApiService){

  }
  ngOnInit(){
   this.apiService.getRawAll().subscribe(data =>{
    console.log(data);
    this.materialmap= data['material'];
    this.rawItemCfg =  data['sizemap'];
    console.log(this.rawItemCfg)
    this.selectedMaterial = Object.keys(this.materialmap)[0];
    console.log("defaultselection",this.selectedMaterial)
    this.sizemap= this.rawItemCfg[this.selectedMaterial]
    console.log(this.sizemap)
   });
  }
  onChange($event){
      console.log($event);
      console.log(this.selectedMaterial)
      this.sizemap= this.rawItemCfg[this.selectedMaterial]
  }
  add(){
    console.log(this.selectedsize)
    console.log(this.weight)
    let obj = new RawMaterialImp('2/2/2',this.selectedsize,this.weight,'industry1')
    console.log(obj)
    this.dataTosave.push(obj)
  }
  edit(indexOfelement){
    console.log("edit",indexOfelement)
  }
  delete(indexOfelement){
      console.log("delete",indexOfelement)
      this.dataTosave.splice(indexOfelement, 1);
  }
}