﻿export class User {
    id: number;
    username: string;
    password: string;
    firstName: string;
    lastName: string;
}
export class RawMaterialCfg {
    id: number;
    industry: string;
    material: string;
    size: string;
}
export class FinishedMaterialCfg {
    id: number;
    industry: string;
    item_name: string;
    unit: string;
    product_type:string;
}
export class RawMaterialImp {
    date : string;
    material : string;
    industry : string;
    weight : number;
    constructor(date:string, material:string, weight:number, industry:string){
        this.date = date;
        this.material = material
        this.industry=  industry;
        this.weight = weight
    }
}
export class FinishedProductImpl {
    date : string;
    product : string;
    weight : number;
    industry: string
    rawMaterialList : Array<RawMaterialImp>=[];
    constructor(date:string, product:string, weight:number, industry:string,material:string,rawmaterial_weight:number){
        this.date = date;
        this.product = product
        this.industry=  industry;
        this.weight = weight
        this.rawMaterialList.push(new RawMaterialImp(date,material,rawmaterial_weight,industry))
    }
}