import { Component,OnInit, Input,Output ,EventEmitter} from '@angular/core';
import {ApiService} from '../_services/api.service';
import { copyStyles } from '@angular/animations/browser/src/util';
//import {UtilService} from '../shared/utilservice'
import {FinishedMaterialCfg} from '../_models'


@Component({
  selector: 'finishedItem',
  templateUrl: './finished_material.html',
  styleUrls: ['./common.css']
})
export class FinishedMaterial implements OnInit {
    uri = 'finishedproduct'
    finishmaterialcfg:Array<FinishedMaterialCfg>
    selectedProduct:FinishedMaterialCfg
    productUnit:string = 'Nos'
    rawMaterialcfg = [] 
    globalcfg =null
    activeProductType:string
    sizemap ={}
    constructor(private apiService:ApiService){
   
     }
     f(selectedProductType){
         console.log(selectedProductType)
         if (this.activeProductType !== selectedProductType){
             console.log("inside if")
             let arrayRawMaterial = this.globalcfg[selectedProductType]
             console.log(arrayRawMaterial)
             this.rawMaterialcfg =[]
             this.activeProductType = selectedProductType
             arrayRawMaterial.forEach(e=>{
                let d = {
                    "label":e,
                    "material":Object.keys(this.sizemap[e])
                }
                this.rawMaterialcfg.push(d)
            })
         }

     }
     ngOnInit(){
        this.apiService.getRawAll().subscribe(data=>{
            this.sizemap = data['sizemap']
            console.log("sizemap ",this.sizemap)
          })
      this.apiService.getFinishedCfgAll().subscribe(resp =>{
          console.log(resp)
          this.finishmaterialcfg = resp['data'];
          this.globalcfg = resp['cfg']
          console.log(this.globalcfg)
          this.selectedProduct = this.finishmaterialcfg[0]
          console.log(this.selectedProduct)          
          this.productUnit =  this.selectedProduct.unit
          this.f(this.selectedProduct.product_type)

      })
      
     }
     onChange($event){
         console.log($event);
         console.log(this.selectedProduct)
         this.f(this.selectedProduct.product_type)
         this.productUnit = this.selectedProduct.unit
       
     }
     add(){
        
     }
}